.commercial {
	position: relative;
	z-index: 5;
	font-size: 0;
	@include rmin(1601) {
		padding-left: (316px / 1850px) * 100%; }
	&__col {
		display: inline-block;
		vertical-align: top;
		@include lg {
			text-align: right;
			width: 50%; }
		+ .commercial__col {
			padding-left: 56px;
			@include lg {
				text-align: left; } } }
	&__btn {
		margin-top: 56px;
		padding-left: 47px;
		@include lg {
			padding-left: 0; }
		.btn-primary {
			line-height: 1;
			padding: 27px 72px; } }
	.list {
		max-width: 384px;
		display: inline-block;
		text-align: left;
		@include lg {
			max-width: 500px; } } }
